<template>
  <div class="layout-px-spacing">
    <portal to="breadcrumb">
      <ul class="navbar-nav flex-row">
        <li>
          <div class="page-header">
            <nav class="breadcrumb-one" aria-label="breadcrumb">
              <ol class="breadcrumb">
                <li class="breadcrumb-item"><a href="javascript:;">Dashboard</a></li>
                <li class="breadcrumb-item active" aria-current="page"><span>Question Categories</span></li>
              </ol>
            </nav>
          </div>
        </li>
      </ul>
    </portal>

    <div class="row layout-top-spacing">
      <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
        <div class="panel p-0">
          <div class="custom-table table3">
            <div class="table-header">
              <div class="d-flex align-items-center">
                <span>Results :</span>
                <span class="ml-2">
                  <b-select v-model="table_option2.page_size" class="h-auto">
                    <b-select-option value="5">5</b-select-option>
                    <b-select-option value="10">10</b-select-option>
                    <b-select-option value="20">20</b-select-option>
                    <b-select-option value="50">50</b-select-option>
                  </b-select>
                </span>
                 
              </div>
            
              
              <div class="header-search">
                <b-input v-model="search" size="sm" placeholder="Search..." />
                <div class="search-image">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    class="feather feather-search"
                  >
                    <circle cx="11" cy="11" r="8"></circle>
                    <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
                  </svg>
                </div>
              </div>
            </div>
  
            <b-table
              ref="basic_table2"
              responsive
              hover
              bordered
              :items="filteredList"
              :fields="columns2"
              :per-page="table_option2.page_size"
              :current-page="table_option2.current_page"
              :filter="table_option2.search_text"
              sort-by="id"
              :show-empty="true"
              @filtered="on_filtered"
              @sort-changed="clear_selection"
            >
            
           
            <template #cell(actions)="data">
              <span v-b-modal="modalUpdateCategory(data.item.id)">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-edit-2 text-success"><path d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"></path></svg>
              </span>
              <b-modal :id="'modalUpdateCategory' + data.item.id" hide-footer title="Modify Category" title-tag="h4" modal-class="register-modal" footer-class="justify-content-center">
                        <form class="mt-0">
                          <div class="form-group">
                            <svg xmlns="http://www.w3.org/2000/svg" aria-hidden="true" role="img" width="24" height="24" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24">
                              <path
                                fill="currentColor"
                                d="m21.558 3.592l-1.15-1.15a1.49 1.49 0 0 0-2.12 0L13 7.731V11h3.27l5.288-5.288a1.49 1.49 0 0 0 0-2.12ZM15.579 9.45h-1.03V8.42L18 4.973l1.03 1.03Z"
                              />
                              <path fill="currentColor" d="M19 19H5V5h6V3H5a2.006 2.006 0 0 0-2 2v14a2.006 2.006 0 0 0 2 2h14a2.006 2.006 0 0 0 2-2v-6h-2Z" />
                            </svg>
                            <b-form-textarea
                              type="text"
                              v-model="data.item.typeC"
                              class="mb-2"
                              :class="[is_submit_updatecat ? (form.typeC && form.typeC.length < 20 && form.typeC.length > 4 ? 'is-valid' : 'is-invalid') : '']"
                              placeholder="Enter your reply content here"
                            ></b-form-textarea>
                            <b-form-valid-feedback>Looks good!</b-form-valid-feedback>
                            <b-form-invalid-feedback :class="{ 'd-block': is_submit_updatecat && !form.typeC }">Please Enter content between 4 and 20 characters</b-form-invalid-feedback>
                            <div class="mt-4 mb-5">
                              <svg xmlns="http://www.w3.org/2000/svg" class="mb-1" aria-hidden="true" role="img" width="24" height="24" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24">
                                <g fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2">
                                  <path d="M2 6a4 4 0 0 1 4-4h12a4 4 0 0 1 4 4v12a4 4 0 0 1-4 4H6a4 4 0 0 1-4-4V6Z" />
                                  <circle cx="8.5" cy="8.5" r="2.5" />
                                  <path d="M14.526 12.621L6 22h12.133A3.867 3.867 0 0 0 22 18.133V18c0-.466-.175-.645-.49-.99l-4.03-4.395a2 2 0 0 0-2.954.006Z" />
                                </g>
                              </svg>
                              <b-file @change="onFileChanged"></b-file>
                            </div>
                          </div>
                          <b-button variant="primary" block class="mt-2 mb-2" @click="updatecat(data.item.typeC , data.item.id)">Update</b-button>
                        </form>
                      </b-modal>
            </template>
             <template #cell(imageCat)="data">
                <span v-if="data.item.imageCat!=null">
                  
              <b-avatar :src="data.item.imageCat" size="4rem" rounded="lg"  alt="" srcset=""/>
               
              </span>
              <span v-else>
                <svg xmlns="http://www.w3.org/2000/svg" aria-hidden="true" role="img" width="4em" height="4em" preserveAspectRatio="xMidYMid meet" viewBox="0 0 64 64"><path fill="currentColor" d="M32 2C15.432 2 2 15.432 2 32.001C2 48.567 15.432 62 32 62s30-13.433 30-29.999C62 15.432 48.568 2 32 2zm22 30.001c0 4.629-1.433 8.922-3.876 12.465l-30.591-30.59A21.889 21.889 0 0 1 32 10c12.15 0 22 9.851 22 22.001zm-44 0a21.9 21.9 0 0 1 3.876-12.468l30.591 30.591A21.887 21.887 0 0 1 32 54.001c-12.15 0-22-9.852-22-22z"/></svg>
              </span>
            </template>
            </b-table>
             
            <div class="table-footer">
              <div class="dataTables_info">Showing {{ meta2.total_items ? meta2.start_index + 1 : 0 }} to {{ meta2.end_index + 1 }} of {{ meta2.total_items }}</div>
              <div class="paginating-container pagination-solid flex-column align-items-right">
                <b-pagination
                  v-model="table_option2.current_page"
                  :total-rows="table_option2.total_rows"
                  :per-page="table_option2.page_size"
                  prev-text="Prev"
                  next-text="Next"
                  first-text="First"
                  last-text="Last"
                  first-class="first"
                  prev-class="prev"
                  next-class="next"
                  last-class="last"
                  class="rounded"
                >
                  <template #first-text>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
                    </svg>
                  </template>
                  <template #prev-text>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
                    </svg>
                  </template>
                  <template #next-text>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
                    </svg>
                  </template>
                  <template #last-text>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 5l7 7-7 7M5 5l7 7-7 7" />
                    </svg>
                  </template>
                </b-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style>
.table3 .actions svg {
  padding: 2px;
}
</style>
<script>
import axios from 'axios'
import { mapGetters, mapActions } from 'vuex';
export default {
  metaInfo: { title: 'Bootstrap Custom Table' },
  data() {
    return {
     

      //table 3
      items2: [],
      columns2: [],
      table_option2: { total_rows: 0, current_page: 1, page_size: 5, search_text: '' },
      meta2: {},
      is_select_all2: false,
      is_submit_updatecat:false,
      selected_rows2: [],
      search:'',
      category:'',
      form:
      {
          typeC:'',

      }
    };
  },
  watch: {
    table_option: {
      handler: function () {
        this.get_meta();
        this.clear_selection();
      },
      deep: true,
    },
    table_option1: {
      handler: function () {
        this.get_meta1();
        this.clear_selection1();
      },
      deep: true,
    },
    table_option2: {
      handler: function () {
        this.get_meta2();
        this.clear_selection2();
      },
      deep: true,
    },
  },
   created: function () {
    this.GetQuestioncategories();
        },
  mounted() {
    this.bind_data();
  },
  computed:{
 ...mapGetters({
      Questioncategories: 'StateQuestioncategories',
  
    }),
     filteredList() {
      return this.Questioncategories.filter((category) => {
        return category.typeC.toLowerCase().includes(this.search.toLowerCase());
      });
    },
    
  },
  methods: {
    ...mapActions(['GetQuestioncategories']),
    modalUpdateCategory(id) {
      return 'modalUpdateCategory' + id;
    },
     onFileChanged(event) {
      this.image = event.target.files[0];
    },
     async updatecat(cat , id) {
      try {
        this.form.typeC = cat;
        this.is_submit_updatecat = true;
        if (this.form.typeC && this.form.typeC.length < 20 && this.form.typeC.length > 4) {
          this.$bvModal.hide('modalUpdateCategory' + id);
          var formdata = new FormData();
          if (this.image != null) {
            formdata.append('imageCat', this.image);
          }
          formdata.append('typeC', this.form.typeC);
         
          await axios.post('/questioncategory/questioncategory-update/' + id + '/', formdata);
         
        this.$swal('Good Job!', 'Category updated successfully !', 'success');
        
          this.is_submit_updatecat = false;
        }
      } catch (error) {
        throw 'Il ya un errora !';
      }
    },
    bind_data() {
      //table 3
      this.columns2 = [
        { key: 'imageCat', label: 'Image', class: 'text-center  ' },
        { key: 'typeC', label: 'Title' },
         { key: 'actions', label: 'Actions', class: 'text-center  ' },
      ],
      

      this.table_option2.total_rows = this.Questioncategories.length;
      this.get_meta2();
    },
    on_filtered(filtered_items) {
      this.refresh_table(filtered_items.length);
    },
    refresh_table(total) {
      this.table_option.total_rows = total;
      this.table_option.currentPage = 1;
    },
    get_meta() {
      this.meta = this.get_common_meta(this.table_option);
    },

    get_common_meta(table_option) {
      var startPage;
      var endPage;
      var totalPages = table_option.page_size < 1 ? 1 : Math.ceil(table_option.total_rows / table_option.page_size);
      totalPages = Math.max(totalPages || 0, 1);

      var maxSize = 5;
      var isMaxSized = typeof maxSize !== 'undefined' && maxSize < totalPages;
      if (isMaxSized) {
        startPage = Math.max(table_option.current_page - Math.floor(maxSize / 2), 1);
        endPage = startPage + maxSize - 1;

        if (endPage > totalPages) {
          endPage = totalPages;
          startPage = endPage - maxSize + 1;
        }
      } else {
        startPage = 1;
        endPage = totalPages;
      }
      let startIndex = (table_option.current_page - 1) * table_option.page_size;
      let endIndex = Math.min(startIndex + table_option.page_size - 1, table_option.total_rows - 1);

      var pages = Array.from(Array(endPage + 1 - startPage).keys()).map((i) => startPage + i);
      return {
        total_items: table_option.total_rows,
        current_page: table_option.current_page,
        page_size: table_option.page_size,
        total_pages: totalPages,
        start_page: startPage,
        end_page: endPage,
        start_index: startIndex,
        end_index: endIndex,
        pages: pages,
      };
    },

    //checkbox selection
    select_all() {
      this.selected_rows = this.$refs.basic_table.paginatedItems.map((d) => {
        return d.id;
      });
      if (!this.is_select_all) {
        this.clear_selection();
      }
      this.check_select_all();
    },
    check_select_all() {
      let ids = this.$refs.basic_table.paginatedItems.map((d) => {
        return d.id;
      });
      this.is_select_all = false;
      if (ids.length == this.selected_rows.length) {
        this.is_select_all = true;
      }
    },
    clear_selection() {
      this.is_select_all = false;
      this.selected_rows = [];
    },

    //table 2
    on_filtered1(filtered_items) {
      this.refresh_table1(filtered_items.length);
    },
    refresh_table1(total) {
      this.table_option1.total_rows = total;
      this.table_option1.currentPage = 1;
    },
    get_meta1() {
      this.meta1 = this.get_common_meta(this.table_option1);
    },
    //checkbox selection
    select_all1() {
      this.selected_rows1 = this.$refs.basic_table1.paginatedItems.map((d) => {
        return d.id;
      });
      if (!this.is_select_all1) {
        this.clear_selection1();
      }
      this.check_select_all1();
    },
    check_select_all1() {
      let ids = this.$refs.basic_table1.paginatedItems.map((d) => {
        return d.id;
      });
      this.is_select_all1 = false;
      if (ids.length == this.selected_rows1.length) {
        this.is_select_all1 = true;
      }
    },
    clear_selection1() {
      this.is_select_all1 = false;
      this.selected_rows1 = [];
    },

    //table 3
    on_filtered2(filtered_items) {
      this.refresh_table2(filtered_items.length);
    },
    refresh_table2(total) {
      this.table_option2.total_rows = total;
      this.table_option2.currentPage = 1;
    },
    get_meta2() {
      this.meta2 = this.get_common_meta(this.table_option2);
    },
    //checkbox selection
    select_all2() {
      this.selected_rows2 = this.$refs.basic_table2.paginatedItems.map((d) => {
        return d.id;
      });
      if (!this.is_select_all2) {
        this.clear_selection2();
      }
      this.check_select_all2();
    },
    check_select_all2() {
      let ids = this.$refs.basic_table2.paginatedItems.map((d) => {
        return d.id;
      });
      this.is_select_all2 = false;
      if (ids.length == this.selected_rows2.length) {
        this.is_select_all2 = true;
      }
    },
    clear_selection2() {
      this.is_select_all2 = false;
      this.selected_rows2 = [];
    },
  },
};
</script>
